import React, { useState, useEffect, setState, Component } from "react";
import "./App.css";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import history from "./history";
import Home from "./Home";
import SignIn from "./login";
import Registros from "./registros";
import "semantic-ui-css/semantic.min.css";
import { Container, Header, Segment, Image, Menu } from "semantic-ui-react";
import Logo from "./images/logo.png";

function App() {
  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);
  const [activeItem, setActiveItem] = useState("cultura");
  const handleItemClick = (e, { name }) => {
    setActiveItem(name);
  };
  return (
    <Router history={history}>
      <Container style={{ marginTop: "3em" }}>
        <Header as="h1" dividing style={{ color: "red" }}>
          <Image avatar src={Logo} />
          SASSO
        </Header>
        <Menu inverted color="red" pointing secondary>
          <Menu.Item
            name="cultura"
            active={activeItem === "cultura"}
            onClick={handleItemClick}
            color="red"
            as={Link}
            to="/"
          >
            CULTURA
          </Menu.Item>

          <Menu.Item
            name="registro"
            active={activeItem === "registro"}
            onClick={handleItemClick}
            as={Link}
            to="/registros"
          >
            REGISTRO
          </Menu.Item>

          <Menu.Item
            name="estadistica"
            active={activeItem === "estadistica"}
            onClick={handleItemClick}
          >
            ESTADISTICA
          </Menu.Item>
        </Menu>
        <div className="vscrolling-wrapper" style={{ height: height * 0.6 }}>
          <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/signin" component={SignIn} />
            <Route path="/registros" component={() => <Registros />} />
          </Switch>
        </div>
      </Container>
    </Router>
  );
}

export default App;
