import React, { Component } from "react";
import { Container, Card, Rating } from "semantic-ui-react";

function Home() {
  const areas = [
    { id: 1, name: "Area de Reciclaje", short: "AR" },
    { id: 2, name: "Linea1", short: "L1" },
    { id: 3, name: "Linea2", short: "L2" },
    { id: 4, name: "Linea3", short: "L3" },
    { id: 5, name: "Linea4", short: "L4" },
    { id: 6, name: "Linea5", short: "L5" },
    { id: 7, name: "PTAR", short: "PT" },
    { id: 8, name: "Sala de Jarabe", short: "SJ" },
    { id: 9, name: "Taller de Mtto", short: "TM" },
    { id: 9, name: "Area de Operaciones", short: "OP" },
  ];

  return (
    <Container>
      <header
        style={{
          fontSize: "24px",
        }}
      >
        <strong>Bienvenido</strong>
      </header>
      <br />

      <Card.Group itemsPerRow={5}>
        {areas.map((item, i) => (
          <Card>
            <Card.Content header={item.short} />
            <Card.Content description={item.name} style={{ fontSize: 10 }} />
            <Card.Content description="BHM" />
            <Card.Content extra>
              <Rating icon="star" defaultRating={4} maxRating={5} />
            </Card.Content>
            <Card.Content description="IAS" />
            <Card.Content extra>
              <Rating icon="star" defaultRating={1} maxRating={5} />
            </Card.Content>
          </Card>
        ))}
      </Card.Group>
    </Container>
  );
}

export default Home;
